<template>

  <TaskCard less="3" title="Математика для детей" v-bind:tech="['JavaScript']" level="Средний" v-if="view === 'card'"/>

  <ul v-if="view === 'note'">
    <Link to="https://learn.javascript.ru/modifying-document"><img src="@/assets/jslearn.png" height="24" alt="">Изменение документа</Link>
    <Link to="https://learn.javascript.ru/array"><img src="@/assets/jslearn.png" height="24" alt="">Массивы</Link>
    <Link to="https://developer.mozilla.org/ru/docs/Web/JavaScript/Reference/Global_Objects/Math/random"><img src="@/assets/mozilla.png" height="24" alt="">Math.random()</Link>
    <Link to="https://developer.mozilla.org/ru/docs/Web/JavaScript/Reference/Global_Objects/Math/ceil"><img src="@/assets/mozilla.png" height="24" alt="">Math.ceil()</Link>
    <Link to="https://learn.javascript.ru/mouse-events-basics"><img src="@/assets/jslearn.png" height="24" alt="">Основы событий мыши</Link>
  </ul>

  <div class="do" v-if="view === 'do'">
    <span>Вам надо разработать "игру" для детей, в которой они смогут порешать простые математические примеры<br>Функционал:</span>
    <ul>
      <li>Игра длится бесконечно</li>
      <li>Пример должен генерироваться сам</li>
      <li>Пример должен состоять только из чисел, которые заканчиваются на 0</li>
      <li>Варианты ответов должны быть перемешаны</li>

    </ul>
  </div>

  <div class="container task_view" v-if="view === 'task'">
    <div class="math_task">{{ task[0] }} + {{ task[1] }} = ?</div>
    <div class="variants">
      <div class="variant" @click="checkComplete(~~$event.target.innerHTML)">{{ variants[0] }}</div>
      <div class="variant" @click="checkComplete(~~$event.target.innerHTML)">{{ variants[1] }}</div>
      <div class="variant" @click="checkComplete(~~$event.target.innerHTML)">{{ variants[2] }}</div>
    </div>
  </div>
</template>

<script>
import Link from "@/components/Link";
import TaskCard from "@/components/TaskCard";


export default {
  name: "MathT",
  components : {
    Link,
    TaskCard
  },
  props : {
    view : {
      type : String
    }
  },
  data(){
    return {
      task : [0,0],
      variants : [],
    }
  },
  methods :{
  roundTo10(i){return Math.ceil(i/10)*10},
    shuffle(array) {
    array.sort(() => Math.random() - 0.5);
  },
  createTask(){
      this.task[0] = this.roundTo10(Math.floor(Math.random() * 500));
      this.task[1] = this.roundTo10(Math.floor(Math.random() * 500));
      let answer = this.task[0] + this.task[1];
      this.variants[0] = this.roundTo10(Math.ceil(answer - answer / 9));
      this.variants[1] = this.roundTo10(Math.ceil(answer / 2));
      this.variants[2] = Math.ceil(answer);
      console.log(this.variants)
      console.log(this.shuffle(this.variants));
    },
    checkComplete(ans){
      if(this.task[0] + this.task[1] === ans) this.createTask();
      else alert("Неверно")
    }
  },
  mounted() {
    this.createTask()
  }
}
</script>

<style scoped lang="scss">
@import "@/assets/styles.scss";
.task_view{
  color: #fff;
  padding: 7vh 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.variants{
  display: flex;

}
.math_task{
  font-size: 1.3em;
}
.variant {
  background: $second-color;
  padding: 10px;
  margin: 0 0.3vw;
  font-size: 1.2em;
  &:hover{
    background: $input-bg-hover;
    cursor: pointer;
  }
}

</style>